body {
    margin: 0;
    padding: 0;
    color:black;
    font-family: 'Open Sans', Helvetica, sans-serif;
    box-sizing: border-box;
    background-color: #dfd9c8;
    background-image:
  /* repeating-linear-gradient(120deg, rgba(255, 255, 255, 0.801), rgba(255, 255, 255, 0.801) 1px, transparent 1px, transparent 60px),
  repeating-linear-gradient(60deg, rgba(255, 255, 255, 0.801), rgba(255, 255, 255, 0.801) 1px, transparent 1px, transparent 60px), */
    linear-gradient(60deg, rgba(214, 208, 208, 0.925) 25%, transparent 25%, transparent 75%,  rgba(214, 208, 208, 0.925) 75%,  rgba(214, 208, 208, 0.925)),
    linear-gradient(120deg, rgba(214, 208, 208, 0.925) 25%, transparent 25%, transparent 75%, rgba(214, 208, 208, 0.925) 75%, rgba(214, 208, 208, 0.925));
    background-size: 70px 120px; 
  
  }
  
  /* Assign grid instructions to our parent grid container, mobile-first (hide the sidenav) */
.grid-container {
  display: grid;
  grid-template-columns: 1fr; /* Side nav is hidden on mobile */
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas:
    'image'
    'about'
    'skills'
    'project'
    'contact';
  height: 100vh;
 
}
  
.menu-icon {
  position: fixed; /* Needs to stay visible for all mobile scrolling */
  display: flex;
  top: 5px;
  left: 10px;
  align-items: center;
  justify-content: center;
  background-color: #DADAE3;
  border-radius: 50%;
  z-index: 1;
  cursor: pointer;
  padding: 12px;
}

#header {
    grid-area: header;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 140px;
    position: fixed;
    overflow-y: auto;
    transform: translateX(-245px);
    transition: all .6s ease-in-out;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
    z-index: 2; /* Needs to sit above the hamburger menu icon */
    background: white;
    
  }
  
  #header.active {
    transform: translateX(0);
  }
  
  .headerCloseIcon {
    position: absolute;
    visibility: visible;
    top: 8px;
    right: 12px;
    cursor: pointer;
    font-size: 20px;
    color: #ddd;
  }
  
  .headerList {
    padding: 0;
    margin-top: 85px;
    list-style-type: none;
    
  }
  
  .headerListItem {
    padding: 20px 20px 20px 40px;
    color: #ddd;
  }
  
  .headerListItem:hover {
    background-color: rgba(255, 255, 255, 0.2);
    cursor: pointer;
  }

  #image {
    grid-area: image;
    width: 100vw;
    height: 50vh;
  
  }

  #about {
    grid-area: about;
   
  }
  
  #skills {
    grid-area: skills;
    
  }
  
  #project {
    grid-area: project;
   
  }
  
  #contact {
    grid-area: contact;
    
  }
  
  
  /* Non-mobile styles, 750px breakpoint */
  @media only screen and (min-width: 46.875em) {
    /* Show the sidenav */
    .grid-container {
        grid-template-columns: 140px 1fr; /* Show the side nav for non-mobile screens */
        grid-template-areas:
        'header image'
        'header about'
        'header skills'
        'header project'
        'header contact';
      }
  
  
    #header {
      position: fixed;
      transform: translateX(0);
    }
  
    #headerCloseIcon {
      visibility: hidden;
    }
  }
  
  .title {
    background-color:  #008073;
    color: white;
    text-align: start;
    height: 60px;
    padding: 5px;
    
  }
  